@font-face {
  font-family: 'SBAggroB';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2108@1.1/SBAggroB.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

.App{
  background: linear-gradient(45deg, #DE6674,#F57EC8);
  height: auto;
  min-height: 100vh;
}
/* #DE6674 */
p{
  font-family: 'SBAggroB';
}

/* 메인페이지 스타일 */

.header{
  font-family: 'SBAggroB';
  text-align: center;
  padding-top: 10vh;
  color: white;
}

.poke_card img{
  width: 80%;
  border: 1px solid gray;
  border-radius: 20px;
  box-shadow: 10px 10px 10px;
}

.poke_card{
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.col-sm p{
  font-size: x-large;
  color: aliceblue;
  text-align: center;
  margin-top: 5%;
}



